export default {
    props:['project','activeRow'],
    data(){
        return{
            objPayment:{}
        }
    },
    methods:{
        sendRequirePayment(){
            console.log('sendRequirePayment',this.objPayment)
            this.$emit('requiredPayment',this.objPayment)
            this.hide()
        },
        updateObjectRequired(obj){
            this.objPayment = {...obj}
        },
        triggerForm(status){
            if(status == 'DRAFT'){
                this.objPayment.status = 'DRAFT'
            }
            else{
                this.objPayment.status = 'PENDING'
            }
            this.$refs.btnSubmit.click()
        },
        show() {
            this.$refs.createRequired.showModal();
        },
        hide(){
            this.$refs.createRequired.hideModal()
        },
    },

}
